.ListeningView {
  background-color: var(--background-secondary);
  display: flex;
  flex-direction: column;
  align-items: center;
  /* margin: 0 auto; */
}

.ListeningView * {
  text-align: center;
  flex: 0;
}

.ListeningView .top-wrapper {
  min-width: 200px;
  width: 200px;
  /* width: 30vw; */
}

.ListeningView img {
  width: 100%;
}

.ListeningView .title {
  font-weight: 700;
  font-size: 1.3em;
  margin-top: 20px;
}

.ListeningView .subtitle {
  font-weight: 300;
  font-size: 0.8em;
  margin-top: 2.5px;
}

.ListeningView .player {
  margin: 20px 0;
  width: 100%;
}

.ListeningView .player .playPause {
  height: 40px;
  /* margin-top: 15px; */
}

.ListeningView .player input[type='range'] {
  width: 100%;
  border: none;
  outline: none;
}

.ListeningView .player input[type='range']::-webkit-slider-runnable-track {
  background: rgb(120, 120, 120);
  border: none;
  outline: none;
  /* border-color: rgb(120, 120, 120); */
  height: 3px;
}

.ListeningView .player input[type='range']::-webkit-slider-thumb {
    margin-top: -7px;
  }

/* .ListeningView audio {
  height: 40px;
} */

.ListeningView .time-wrapper {
  position: relative;
}

.ListeningView .time-wrapper .time {
  /* position: absolute; */
  /* top: -2px; */
  line-height: 40px;
}

.ListeningView .time-wrapper .left {
  /* left: -45px; */
}

.ListeningView .time-wrapper .right {
  /* right: -45px; */
}

