.addNote {
  background-color: #338B31;
  text-align: center;
  justify-content: center;
  border: none;
  border-radius: 8px;
  color: white;
  height: 50px;
  width: 150px;
  font-size: 15px;
  padding: 8px 0;
}

.audioPlayer {
  display: grid;
  grid-template-columns: 64px 1fr 64px;
  grid-template-rows: 36px 40px;
}

.audioPlayer > *:nth-child(1) {
  grid-column-start: 1;
  grid-column-end: 4;
}
.audioPlayer > * {
  /* grid-row: 2 / 1;
  grid-column: 1 / 1; */
}