.Navbar {
  background-color: var(--navbar-background);
  display: flex;
  flex: 0;
}

.NavbarButton {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  padding: 0.2em;
  background: none;
  border: none;
}

.NavbarButton > img {
  height: 48px;
  image-rendering: pixelated;
}